"use client";
import { useState, useEffect } from "react";
import { Progress } from "@/components/ui/progress";
import { Button } from "@/components/ui/button";
import {
  Film,
  Users,
  Clock,
  Plus,
  AlertCircle,
  Database,
  CalendarDays,
  Ticket,
  ChevronUp,
  ChevronDown,
  Info,
  Loader2,
} from "lucide-react";
import { Badge } from "@/components/ui/badge";
import {
  Tooltip,
  TooltipTrigger,
  TooltipContent,
} from "@/components/ui/tooltip";
import { Skeleton } from "@/components/ui/skeleton";

export function SidePanelOverview() {
  const [stats, setStats] = useState(null);
  const [isLoading, setIsLoading] = useState(true);
  const [darkMode, setDarkMode] = useState(false);
  const [openSections, setOpenSections] = useState({
    stats: true,
    activity: true,
    actions: true,
  });

  // Simulate real-time updates
  useEffect(() => {
    const fetchData = async () => {
      try {
        // Replace with actual API call
        const mockData = {
          totalMovies: 245 + Math.floor(Math.random() * 10),
          newThisWeek: 12 + Math.floor(Math.random() * 3),
          activeUsers: 1542 + Math.floor(Math.random() * 50),
          storageUsage: 65 + Math.floor(Math.random() * 5),
          recentActivities: [
            { id: 1, title: "The Dark Knight added", time: "2h ago" },
            { id: 2, title: "User subscription", time: "4h ago" },
            { id: 3, title: "System updated", time: "1d ago" },
          ],
          systemStatus: "operational",
        };

        setStats(mockData);
        setIsLoading(false);
      } catch (error) {
        console.error("Error fetching data:", error);
      }
    };

    // Initial fetch
    fetchData();

    // Update every 30 seconds
    const interval = setInterval(fetchData, 30000);

    // Dark mode detection
    setDarkMode(window.matchMedia("(prefers-color-scheme: dark)").matches);

    return () => clearInterval(interval);
  }, []);

  const toggleSection = (section) => {
    setOpenSections((prev) => ({
      ...prev,
      [section]: !prev[section],
    }));
  };

  const handleActivityClick = (activityId) => {
    console.log("Activity clicked:", activityId);
    // Add your click handler logic here
  };

  if (isLoading) {
    return (
      <div className="h-full p-4 space-y-6 bg-background/95 backdrop-blur border-l dark:bg-gray-900">
        {[...Array(4)].map((_, i) => (
          <Skeleton key={i} className="h-24 w-full rounded-lg" />
        ))}
      </div>
    );
  }

  return (
    <div
      className={`h-full p-4 space-y-6 backdrop-blur border-l 
      ${darkMode ? "dark:bg-gray-900 bg-gray-50" : "bg-white"}`}
    >
      {/* Header with Dark Mode Toggle */}
      <div className="flex items-center justify-between">
        <div className="space-y-1">
          <h3 className="text-lg font-semibold flex items-center gap-2">
            <Database className="h-5 w-5 text-primary dark:text-primary-400" />
            System Overview
          </h3>
          <div className="text-sm text-muted-foreground flex items-center gap-2">
            <CalendarDays className="h-4 w-4" />
            {new Date().toLocaleDateString()}
          </div>
        </div>
        <Button
          variant="ghost"
          size="sm"
          onClick={() => setDarkMode(!darkMode)}
          className="text-muted-foreground"
        >
          {darkMode ? "üåô" : "‚òÄÔ∏è"}
        </Button>
      </div>

      {/* Collapsible Stats Section */}
      <div className="space-y-4">
        <div className="flex items-center justify-between">
          <h4 className="font-medium flex items-center gap-2">
            Key Metrics
            <Tooltip>
              <TooltipTrigger>
                <Info className="h-4 w-4" />
              </TooltipTrigger>
              <TooltipContent>
                <p>Real-time system statistics</p>
              </TooltipContent>
            </Tooltip>
          </h4>
          <Button
            variant="ghost"
            size="sm"
            onClick={() => toggleSection("stats")}
          >
            {openSections.stats ? <ChevronUp /> : <ChevronDown />}
          </Button>
        </div>

        {openSections.stats && (
          <div className="space-y-4">
            <div className="space-y-2">
              <div className="flex items-center justify-between">
                <div className="flex items-center gap-2 text-sm">
                  <Film className="h-4 w-4" />
                  Total Movies
                  <Tooltip>
                    <TooltipTrigger>
                      <Info className="h-3 w-3" />
                    </TooltipTrigger>
                    <TooltipContent>
                      <p>Total movies in database</p>
                      <p className="text-xs">Updated every 30 seconds</p>
                    </TooltipContent>
                  </Tooltip>
                </div>
                <Badge variant="outline">
                  {stats.totalMovies}
                  {isLoading && (
                    <Loader2 className="ml-2 h-3 w-3 animate-spin" />
                  )}
                </Badge>
              </div>
              <Progress
                value={(stats.newThisWeek / stats.totalMovies) * 100}
                className="h-2 dark:bg-gray-700"
              />
              <p className="text-xs text-muted-foreground">
                +{stats.newThisWeek} this week
              </p>
            </div>

            <div className="space-y-2">
              <div className="flex items-center justify-between">
                <div className="flex items-center gap-2 text-sm">
                  <Users className="h-4 w-4" />
                  Active Users
                </div>
                <Badge variant="outline">
                  {stats.activeUsers}
                  {isLoading && (
                    <Loader2 className="ml-2 h-3 w-3 animate-spin" />
                  )}
                </Badge>
              </div>
              <Progress value={75} className="h-2 dark:bg-gray-700" />
              <p className="text-xs text-muted-foreground">
                +12.5% from last month
              </p>
            </div>
          </div>
        )}
      </div>

      {/* System Status */}
      <div
        className={`p-4 rounded-lg transition-all ${
          darkMode ? "bg-gray-800" : "bg-muted/50"
        }`}
      >
        <div className="flex items-center gap-2">
          <AlertCircle
            className={`h-5 w-5 ${
              stats.systemStatus === "operational"
                ? "text-green-500"
                : "text-red-500"
            }`}
          />
          <div>
            <p className="text-sm font-medium">
              All Systems {stats.systemStatus}
              <Tooltip>
                <TooltipTrigger>
                  <Info className="ml-2 h-3 w-3" />
                </TooltipTrigger>
                <TooltipContent>
                  <p>Current system health status</p>
                </TooltipContent>
              </Tooltip>
            </p>
            <p className="text-xs text-muted-foreground">
              Storage: {stats.storageUsage}% used
            </p>
          </div>
        </div>
      </div>

      {/* Collapsible Quick Actions */}
      <div className="space-y-2">
        <div className="flex items-center justify-between">
          <h4 className="font-medium">Quick Actions</h4>
          <Button
            variant="ghost"
            size="sm"
            onClick={() => toggleSection("actions")}
          >
            {openSections.actions ? <ChevronUp /> : <ChevronDown />}
          </Button>
        </div>
        {openSections.actions && (
          <div className="grid grid-cols-2 gap-2">
            <Button
              variant="outline"
              className="h-10 flex items-center gap-2 dark:hover:bg-gray-700"
            >
              <Plus className="h-4 w-4" />
              <span className="text-xs">Add Movie</span>
            </Button>
            <Button
              variant="outline"
              className="h-10 flex items-center gap-2 dark:hover:bg-gray-700"
            >
              <Ticket className="h-4 w-4" />
              <span className="text-xs">Manage Content</span>
            </Button>
          </div>
        )}
      </div>

      {/* Collapsible Recent Activity */}
      <div className="space-y-2">
        <div className="flex items-center justify-between">
          <h4 className="font-medium flex items-center gap-2">
            <Clock className="h-4 w-4" />
            Recent Activity
          </h4>
          <Button
            variant="ghost"
            size="sm"
            onClick={() => toggleSection("activity")}
          >
            {openSections.activity ? <ChevronUp /> : <ChevronDown />}
          </Button>
        </div>
        {openSections.activity && (
          <div className="space-y-3">
            {stats.recentActivities.map((activity) => (
              <button
                key={activity.id}
                onClick={() => handleActivityClick(activity.id)}
                className={`w-full text-left p-2 rounded-lg transition-colors ${
                  darkMode ? "hover:bg-gray-700" : "hover:bg-gray-100"
                }`}
              >
                <div className="flex items-start gap-3">
                  <div className="h-2 w-2 mt-2 rounded-full bg-primary" />
                  <div>
                    <p className="text-sm leading-none">{activity.title}</p>
                    <p className="text-xs text-muted-foreground">
                      {activity.time}
                    </p>
                  </div>
                </div>
              </button>
            ))}
          </div>
        )}
      </div>
    </div>
  );
}

